html {
  font-family: 'Source Code Pro', monospace;
  font-size: 10px; }

body {
  background-color: gold; }

.active {
  background-color: rgba(255, 215, 0, 0.5);
  border: 2px solid rgba(223, 179, 10, 0.5); }

.keyboard__button {
  width: 50px;
  height: 50px;
  padding: 0;
  margin: 3px;
  display: inline-flex;
  align-items: center;
  justify-content: center;
  vertical-align: top;
  font-size: 1.4rem;
  color: #333;
  border-radius: 3px;
  border: 1px solid #c2c2c2;
  background-color: rgba(224, 224, 224, 0.8);
  outline: none;
  cursor: pointer;
  transition: all 0.3s ease 0s; }
  .keyboard__button > * {
    display: none; }
  .keyboard__button_wide {
    width: 75px; }
  .keyboard__button_the-widest {
    width: 325px; }
  .keyboard__button_base-width {
    width: 50px; }
  .keyboard__button_wider {
    width: 100px; }
  .keyboard__button .current {
    display: block; }
  .keyboard__button .small {
    display: none; }
  .keyboard__button .big {
    display: none; }
  .keyboard__button .on {
    display: block; }

.textarea {
  margin-bottom: 20px;
  color: #333;
  font-size: 1.5rem;
  font-family: monospace;
  font-weight: 600;
  border: 1px solid transparent;
  border-radius: 10px;
  box-shadow: 5px 5px 5px rgba(0, 0, 0, 0.3);
  overflow: hidden; }
  .textarea:focus {
    outline: none;
    border: 1px solid rgba(85, 221, 255, 0.5); }

.keyboard-container {
  width: 895px;
  text-align: center;
  background-color: rgba(255, 255, 255, 0.85);
  border-radius: 5px;
  padding: 10px;
  box-shadow: 5px 4px 8px rgba(0, 0, 0, 0.3); }

.wrapper {
  width: 1200px;
  margin: 0 auto;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center; }
